<template>
  <div>
    <el-dialog
        :visible.sync="neo4jDialog"
        size="tiny"
        width="500px"
        class="dialog"
    >
      <el-tabs v-model="neo4jtab">
        <el-tab-pane :label="$lang.neo4j.createN" name="create">
          <el-form ref="form" :model="create_form" label-width="100px">
            <el-form-item :label="$lang.neo4j.disease" prop="text">
              <el-input
                  class="neo4j_input"
                  v-model="create_form.disease"
                  :placeholder="$lang.messages.enter + ' ' + $lang.neo4j.disease"
                  size="small"
                  style="width:200px"
              ></el-input>
            </el-form-item>
            <el-form-item :label="$lang.neo4j.label" prop="text">
              <el-input
                  class="neo4j_input"
                  v-model="create_form.node_label"
                  :placeholder="$lang.messages.enter + ' ' + $lang.neo4j.node_label"
                  size="small"
                  style="width:200px"
              ></el-input>
            </el-form-item>
            <el-form-item :label="$lang.neo4j.name" prop="text">
              <el-input
                  class="neo4j_input"
                  v-model="create_form.node_name"
                  :placeholder="$lang.messages.enter + ' ' + $lang.neo4j.node_name"
                  size="small"
                  style="width:200px"
              ></el-input>
            </el-form-item>
            <p>
              <small>{{ $lang.messages.createConfigurableProject }}</small>
            </p>
          </el-form>
        </el-tab-pane>

        <el-tab-pane :label="$lang.neo4j.modifyN" name="upload">
          <el-form ref="form" :model="modify_form" label-width="100px">
            <el-form-item :label="$lang.neo4j.label" prop="text" style="display: inline">
              <el-input
                  class="neo4j_input"
                  v-model="modify_form.node_label"
                  :placeholder="$lang.messages.enter + ' ' + $lang.neo4j.node_label"
                  size="small"
                  style="width:200px"
              ></el-input>
            </el-form-item>
            <el-form-item :label="$lang.neo4j.name" prop="text" style="display: inline">
              <el-input
                  class="neo4j_input"
                  v-model="modify_form.node_name"
                  :placeholder="$lang.messages.enter + ' ' + $lang.neo4j.node_name"
                  size="small"
                  style="width:200px"
              ></el-input>
            </el-form-item>
            <el-form-item :label="$lang.neo4j.after_name" prop="text" style="display: inline">
              <el-input
                  class="neo4j_input"
                  v-model="modify_form.after_name"
                  :placeholder="$lang.messages.enter + ' ' + $lang.neo4j.after_name"
                  size="small"
                  style="width:200px"
              ></el-input>
            </el-form-item>
          </el-form>
        </el-tab-pane>

        <el-tab-pane :label="$lang.neo4j.deleteN" name="clone">
          <el-form ref="form" :model="delete_form" label-width="100px">
            <el-form-item :label="$lang.neo4j.label" prop="text" style="display: inline">
              <el-input
                  class="neo4j_input"
                  v-model="delete_form.node_label"
                  :placeholder="$lang.messages.enter + ' ' + $lang.neo4j.node_label"
                  size="small"
                  style="width:200px"
              ></el-input>
            </el-form-item>
            <el-form-item :label="$lang.neo4j.name" prop="text" style="display: inline">
              <el-input
                  class="neo4j_input"
                  v-model="delete_form.node_name"
                  :placeholder="$lang.messages.enter + ' ' + $lang.neo4j.node_name"
                  size="small"
                  style="width:200px"
              ></el-input>
            </el-form-item>
          </el-form>
        </el-tab-pane>
      </el-tabs>

      <div slot="footer">
        <el-button @click="neo4jDialog = false" size="small"
        >{{ $lang.buttons.cancel }}
        </el-button>
        <el-button
            @click="onSubmitCreateForm()"
            v-if="neo4jtab === 'create'"
            type="primary"
            size="small"
        >{{ $lang.buttons.create }}
        </el-button>
        <el-button
            @click="onSubmitModifyForm()"
            v-if="neo4jtab === 'upload'"
            type="primary"
            size="small"
        >{{ $lang.buttons.modify }}
        </el-button>
        <el-button
            @click="onSubmitDeleteForm()"
            v-if="neo4jtab === 'clone'"
            type="primary"
            size="small"
        >{{ $lang.buttons.delete }}
        </el-button>
      </div>
    </el-dialog>

    <el-row :gutter="24">
      <div class="panel m-l-md">
        <panel-title :title="$lang.neo4j.title">
          <el-button @click="$router.go(0)" size="mini">
            <i class="fa fa-refresh"></i>
            {{ $lang.buttons.refresh }}
          </el-button>
          <el-button
              @click="clickTo"
              type="success"
              size="mini">
            <i class="fa fa-plus"></i>
            {{ $lang.buttons.advance }}
          </el-button>

        </panel-title>
        <div
            class="panel-body"
        >
          <el-row :gutter="24">
            <el-col :span="16">
              <div class="handle-box">
                <el-button
                    type="primary"
                    class="handle-bas mr10"
                    @click="neo4jDialog = true"
                ><i class="fa fa-book"></i>
                  Basic Operation
                </el-button>
                <el-input
                    v-model="find_form.label"
                    :placeholder="$lang.messages.enter + ' ' + $lang.neo4j.label"
                    class="handle-input mr10"
                    style="width: 300px;display: inline-block;"></el-input>
                <el-input
                    v-model="find_form.disease"
                    :placeholder="$lang.messages.enter + ' ' + $lang.neo4j.disease"
                    class="handle-input mr10"
                    style="width: 300px;display: inline-block;"></el-input>
                <el-button type="primary"
                           icon="el-icon-search"
                           @click="onSubmitFindForm()"
                           :loading="onSubmitLoading_find"
                >Find
                </el-button>
              </div>

              <el-table
                  :data="tableData"
                  border
                  class="table"
                  ref="multipleTable"
                  header-cell-class-name="table-header"
                  :empty-text="$lang.messages.noData"
                  v-loading="loading"
                  :element-loading-text="$lang.messages.loading"
              >
                <el-table-column type="selection" width="55" align="center"></el-table-column>
                <el-table-column prop="Relation" label="relation"></el-table-column>
                <el-table-column prop="Node content" label="Nodes content"></el-table-column>
              </el-table>
            </el-col>

            <el-col :span="8">
              <el-table
                  :data="labels_Data"
                  class="table"
                  ref="multipleTable"
                  header-cell-class-name="table-header"
                  :header-cell-style="{background:'#F5F7FA',color:'#606266', height:'10px', padding:'0px'}"
                  :empty-text="$lang.messages.noData"
                  v-loading="loading"
                  :element-loading-text="$lang.messages.loading">
                <el-table-column
                    prop="label_1"
                    label="label name"
                >
                </el-table-column>
                <el-table-column
                    prop="relation"
                    label="Relation"
                >
                </el-table-column>
                <el-table-column
                    prop="label_2"
                    label="label name"
                >
                </el-table-column>
              </el-table>

              <div id="chartPie" style="width:100%; height:400px;margin-top: 10px"></div>

              <el-form label-width="100px">
                <el-form-item :label="$lang.columns.Logs_of_query" prop="output"></el-form-item>
                <el-divider></el-divider>
                <div id="canvas"
                     style="border:1px solid #e4e4e4;
                       height: 400px;border-radius: 0.5rem;"
                     v-if="showinfo">
                  <div v-text="info" style="margin-left: 10px">
                  </div>
                </div>
                <div id="canvas_2"
                     style="border:1px solid #e4e4e4;
                       height: 400px;border-radius: 0.5rem;"
                     v-if="showinfo_neo4j"
                >
                  <div id="viz" style="border:1px solid #e4e4e4;
                       height: 400px;border-radius: 0.5rem;"></div>
                </div>

              </el-form>
            </el-col>
          </el-row>
        </div>
      </div>
    </el-row>
  </div>
</template>


<script>
import PanelTitle from "../../components/PanelTitle";
import echarts from 'echarts'
import $ from 'jquery'

export default {
  name: "ProjectIndex",
  data() {
    return {
      tableData: [],
      labels_Data: [],
      query: {
        address: '',
        name: '',
        pageIndex: 1,
        pageSize: 10
      },
      create_form: {
        disease: "",
        node_label: "",
        node_name: "",
      },
      modify_form: {
        node_label: "",
        node_name: "",
        after_name: "",
      },
      delete_form: {
        node_label: "",
        node_name: "",
      },
      find_form: {
        label:"",
        disease: "",
      },
      info: {},
      showinfo: false,
      showinfo_neo4j: true,
      neo4jtab: "upload",
      neo4jDialog: false,
      onSubmitLoading_find: false,
      loading: false,
      chartPie: null,
      statistic_neo4j: [],
      viz: {},
    };
  },
  components: {
    PanelTitle,
  },
  created() {
    this.getNeo4jStatus();
    this.getchart();
    this.drawPieChart()
  },
  methods: {
    clickTo() {
      window.location.href = "neo4j_graph.html"
    },
    onSubmitCreateForm() {
      this.showinfo_neo4j = false;
      this.showinfo = true;
      this.$http
          .post(this.$store.state.url.neo4j.create, this.create_form)
          .then(({data: info}) => {
            this.info = info;
            console.log(info);
            this.$message.success(this.$store.getters.$lang.messages.successSave);
            this.loading = false;
            this.neo4jDialog = false;
          })
          .catch(() => {
            this.loading = false;
            this.$message.error(this.$store.getters.$lang.messages.errorSave);
          });
    },
    onSubmitModifyForm() {
      this.showinfo_neo4j = false;
      this.showinfo = true;
      this.$http
          .post(this.$store.state.url.neo4j.modify, this.modify_form)
          .then(({data: info}) => {
            this.info = info;
            console.log(info);
            this.$message.success(this.$store.getters.$lang.messages.successSave);
            this.loading = false;
            this.showinfo_neo4j = false;
            this.showinfo = true;
            this.neo4jDialog = false;
          })
          .catch(() => {
            this.loading = false;
            this.$message.error(this.$store.getters.$lang.messages.errorSave);
          });
    },
    onSubmitDeleteForm() {
      this.showinfo_neo4j = false;
      this.showinfo = true;
      this.$http
          .post(this.$store.state.url.neo4j.delete, this.delete_form)
          .then(({data: info}) => {
            this.info = info;
            console.log(info);
            this.$message.success(this.$store.getters.$lang.messages.successSave);
            this.showinfo_neo4j = false;
            this.loading = false;
            this.showinfo = true;
            this.neo4jDialog = false;
          })
          .catch(() => {
            this.loading = false;
            this.$message.error(this.$store.getters.$lang.messages.errorSave);
          });
    },
    onSubmitFindForm() {
      this.showinfo = false;
      this.showinfo_neo4j = true;
      this.onSubmitLoading_find = true;
      this.$http
          .post(this.$store.state.url.neo4j.find, this.find_form)
          .then(response => {
            this.tableData = response.data.find;
            this.draw(response.data.query);
            console.log(response);
            this.$message.success(this.$store.getters.$lang.messages.successSave);
            this.loading = false;
            this.onSubmitLoading_find = false;
          })
          .catch(() => {
            this.onSubmitLoading_find = false;
            this.loading = false;
          });
    },
    getNeo4jStatus() {
      this.loading = true;
      this.$http
          .get(this.$store.state.url.neo4j.get_labels)
          .then(({data: labels_Data}) => {
            this.labels_Data = labels_Data;
            this.loading = false;
            console.log(labels_Data);
          })
          .catch(() => {
            this.loading = false;
          });
    },
    getchart() {
      this.loading = true;
      this.$http
          .get(this.$store.state.url.neo4j.get_chart)
          .then(response => {
            this.drawPieChart(response.data.legend,response.data.chart);
            this.loading = false;
            console.log(response);
          })
          .catch(() => {
            this.loading = false;
          });
    },
    draw(data) {
      const config = {
        encrypted: "ENCRYPTION_ON",
        trust: "TRUST_SYSTEM_CA_SIGNED_CERTIFICATES",
        container_id: "viz",
        server_url: "neo4j://227816f4.databases.neo4j.io",
        server_user: "neo4j",
        server_password: "0A2TcqorfsDg-ai2Brr3YUDuHnYR3UGyeOm5dcWKHgo",
        labels: {},
        relationships: {},
        // Query node statement , Write your
        initial_cypher: data
      };
      this.viz = new NeoVis.default(config);
      this.viz.render();
      console.log(this.viz);
    },
    drawPieChart(legend,chart) {
      this.loading = true;
      this.chartPie = echarts.init(document.getElementById('chartPie'));
      this.chartPie.setOption({
        title: {
          text: 'Pie Chart',
          subtext: 'overall of node labels',
          x: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          orient: 'vertical',
          left: 'left',
          data: legend,
        },
        series: [
          {
            name: 'node labels',
            type: 'pie',
            radius: '55%',
            center: ['50%', '60%'],
            data: chart,
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      });
    },
  },
}
;
</script>

<style lang="scss">
#project-index {
  .dialog {
    .el-dialog__body {
      padding-bottom: 0;
      padding-left: 40px;
      padding-right: 40px;
    }
  }
}

.neo4j_button {
  width: 70px;
  float: right;
  margin-top: 10px !important;

}

.handle-box {
  margin-bottom: 20px;
}

.handle-select {
  width: 120px;
}

.handle-input {
  width: 300px;
  display: inline-block;
}

.table {
  width: 100%;
  font-size: 14px;
}

.red {
  color: #ff0000;
}

.mr10 {
  margin-right: 10px;
}

.table-td-thumb {
  display: block;
  margin: auto;
  width: 40px;
  height: 40px;
}
</style>
